/****************************************************************************************/
/* Client Specific Styling
/****************************************************************************************/
:root {
  font-family: 'helvetica neue', helvetica, sans-serif; }

:root {
  font-family: "helvetica neue", "helvetica", times, serif; }

body {
  color: #4a4a4a;
  font-family: "helvetica neue", "helvetica", times, serif; }

p {
  line-height: 2; }

:root {
  font-size: 62.5%; }

/****************************************************************************************/
/* Client default colors, button colors + background colors */
/****************************************************************************************/
.has-color-green-color {
  color: #1b1b1b !important; }

.has-color-pink-color {
  color: #333333 !important; }

.has-color-white-color {
  color: #ffffff !important; }

.has-color-black-color {
  color: #000000 !important; }

.has-color-gray-color {
  color: #4a4a4a !important; }

.bg-primary {
  background-color: #1b1b1b; }

.bg-secondary {
  background-color: #333333; }

.bg-success {
  background-color: #21a77a; }

.bg-info {
  background-color: #f7f7f9; }

.bg-warning {
  background-color: #ec971f; }

.bg-danger {
  background-color: #843534; }

.bg-dark {
  background-color: #000000; }

/****************************************************************************************/
/* Client buttons */
/****************************************************************************************/
.cortextoo button,
.btn,
#topnav input[type="submit"],
.wp-block-button__link,
.editor-styles-wrapper .button,
.editor-styles-wrapper .button-primary,
.editor-styles-wrapper .mce-content-body.button,
.editor-styles-wrapper .wp-block-button__link {
  text-transform: uppercase;
  border-radius: 34px;
  font-weight: 500; }

.btn,
.editor-styles-wrapper .button,
.editor-styles-wrapper .button-primary,
.editor-styles-wrapper .mce-content-body.button,
.cortextoo button,
.entry-content .btn,
.entry-content .btn:visited,
.entry-content button,
.entry-content input[type="button"],
.entry-content input[type="reset"],
.entry-content input[type="submit"],
.entry-content .button,
.entry-content .wp-block-button:not(.is-style-outline) .wp-block-button__link,
.wp-block-button__link,
.wp-block-button:not(.is-style-outline) .wp-block-button__link.mce-content-body,
.wp-block-file__button,
.wp-block-file .wp-block-file__button {
  font-family: "helvetica neue", "helvetica", times, serif; }

.btn-secondary,
.cortextoo .entry-content .btn-secondary {
  background-color: #333333; }

.btn-signup .wp-block-button__link:before {
  display: inline;
  content: "\f005";
  font: normal normal normal 1.4rem/1 FontAwesome;
  color: #fff;
  padding-right: 8px; }

.cortextoo button:hover,
.btn:hover,
.wp-block-button__link:hover,
.editor-styles-wrapper .button:hover,
.editor-styles-wrapper .button-primary:hover,
.editor-styles-wrapper .mce-content-body.button:hover,
.editor-styles-wrapper .wp-block-button__link:hover {
  opacity: 0.8;
  color: #fff; }

/* tags */
.i-tag {
  font-size: 1.6rem;
  letter-spacing: 0.137rem; }

.i-tag:before {
  display: inline;
  content: "\f02b";
  font: normal normal normal 1.4rem/1 FontAwesome;
  color: #1b1b1b;
  padding-right: 4px; }

/****************************************************************************************/
/* Client navbar */
/****************************************************************************************/
#wrapper-navbar {
  z-index: 1020;
  background-color: #ffffff; }

.navbar {
  padding: 5px 0px;
  background-color: #ffffff; }

.navbar-small {
  max-height: 55px;
  -webkit-transition: opacity 0.4s ease-in-out;
  transition: opacity 0.4s ease-in-out; }

.navbar-small .navbar-brand img {
  max-height: 45px;
  width: auto; }

.navbar-nav li a {
  text-transform: uppercase;
  font-size: 1.5rem;
  line-height: 1;
  color: rgba(43, 43, 43, 0.77);
  letter-spacing: 0.09rem; }

.nav-search,
.nav-toggle {
  display: inline-block;
  font-size: 2rem;
  line-height: 2; }

.nav-search {
  margin-right: 10px; }

@media only screen and (min-width: 1px) {
  .header-navbar {
    height: 55px; }
  .navbar-brand img {
    width: auto;
    max-height: 45px;
    height: auto; }
  #page .navbar-small-mobile {
    padding-bottom: 0px; }
  .navbar {
    display: block; }
  .navbar .container .navbar-small-buttons {
    -webkit-box-pack: end;
    -ms-flex-pack: end;
    justify-content: flex-end; }
  .btn-nav-search,
  .navbar-toggler {
    margin: 0px;
    padding: 5px; } }

@media only screen and (min-width: 768px) {
  .navbar-brand img {
    width: 285px;
    max-height: 92px;
    height: auto; }
  .navbar-nav {
    width: 80%; }
  .header-navbar {
    height: 115px; } }

@media only screen and (min-width: 992px) {
  .nav-search {
    display: none; } }

/****************************************************************************************/
/* Client Typography */
/****************************************************************************************/
.entry-content,
.editor-styles-wrapper p,
.editor-styles-wrapper .container p,
.editor-styles-wrapper .editor-rich-text .mce-content-body p,
.editor-styles-wrapper .wp-block-paragraph,
.editor-styles-wrapper .wp-block-quote p,
.editor-styles-wrapper .wp-block-file,
.wp-block-pullquote,
.wp-block-pullquote blockquote p,
.editor-default-block-appender textarea.editor-default-block-appender__content,
#wrapper-footer {
  font-size: 2rem;
  word-wrap: break-word;
  font-family: "helvetica neue", "helvetica", times, serif; }

/* Responsive Typography */
@media only screen and (max-width: 575px) {
  .entry-content p[class$="-font-size"] {
    line-height: 1; } }

@media only screen and (min-width: 1px) {
  /* section heading accents */
  .editor-styles-wrapper .editor-rich-text .section-heading-accent,
  .section-heading-accent,
  .editor-styles-wrapper .editor-rich-text .section-heading-accent-alt,
  .section-heading-accent-alt,
  .entry-content .section-heading-accent-alt,
  .entry-content h3.section-heading-accent,
  .entry-content h3.section-heading-accent-alt {
    margin: 10px auto;
    font-size: 1.6rem;
    font-weight: 500;
    line-height: 1;
    letter-spacing: 0.08rem;
    text-align: center;
    text-transform: uppercase;
    color: #000000; } }

@media only screen and (min-width: 768px) {
  /* subheadings */
  .entry-content .subheading1,
  .entry-content .subheading2,
  .entry-content .subheading3,
  .entry-content .subheading4,
  .entry-content .subheading5,
  .wp-block-heading .subheading1,
  .wp-block-heading .subheading2,
  .wp-block-heading .subheading3,
  .wp-block-heading .subheading4,
  .wp-block-heading .subheading5 {
    font-weight: 400;
    font-size: 2.6rem;
    letter-spacing: 0.055rem; }
  .entry-content .subheading2,
  .wp-block-heading .subheading2,
  .subheading2 {
    font-weight: 500;
    font-size: 2.3rem;
    letter-spacing: 0.12rem; }
  .entry-content .subheading3,
  .wp-block-heading .subheading3,
  .subheading3 {
    font-weight: 100;
    font-size: 2rem;
    letter-spacing: 0.05rem;
    text-transform: uppercase;
    line-height: 1.2; }
  .entry-content .subheading4,
  .wp-block-heading .subheading4,
  .subheading4 {
    font-size: 1.6rem;
    letter-spacing: 0.001rem; }
  .entry-content .subheading5,
  .wp-block-heading .subheading5,
  .subheading5 {
    font-size: 1.4rem;
    letter-spacing: 0.125rem;
    font-weight: 400; }
  .icon-label,
  .entry-content .icon-label,
  .wp-block-heading .icon-label {
    text-transform: uppercase;
    font-weight: 600;
    font-size: 1.8rem !important;
    letter-spacing: 0.2rem;
    line-height: 1.1; }
  .entry-meta,
  .entry-meta a {
    text-transform: uppercase;
    letter-spacing: 0.05rem;
    line-height: 1;
    font-size: 1.4rem;
    color: #2b2b2b; }
  .entry-footer .comments-link {
    display: block;
    text-align: right;
    text-transform: uppercase;
    font-size: 1.2rem; }
  /* WordPress Gutenberg Font Sizes */
  .has-small-font-size {
    font-size: 1.4rem !important;
    line-height: 1.5 !important;
    letter-spacing: 0.125rem; } }

/****************************************************************************************/
/* Client page title header on logins, search, pages, posts, etc. */
/****************************************************************************************/
.page-template-fullwidthpage .cortextoo .entry-header,
.container-width-page-wrapper.cortextoo .page .entry-header {
